import * as types from './mutation-types'

// action mutations
export default {
  // 
  storeNewsInfo ({ commit }, news) {
    commit(types.STORE_NEWS_INFO, news)
  },
  // 
  setCurrentNews ({ commit }, id) {
    commit(types.SET_CURRENT_NEWS, id)
  },
  // 
  toggleSidebar ({ commit }) {
    commit(types.TOGGLE_SIDEBAR)
  },
  // 
  toggleNightPattern ({ commit }) {
    commit(types.TOGGLE_NIGHT_PATTERN)
  },
  // 
  setCollectList ({ commit }) {
    commit(types.SET_COLLECT_LIST)
  },
  //  
  reduceCollectList ({ commit }, id) {
    commit(types.REDUCE_COLLECT_LIST, id)
  },
  // 
  continueLoadMore ({ commit }) {
    commit(types.CONTINUE_LOAD_MORE)
  },
  // 
  stopLoadMore ({ commit }) {
    commit(types.STOP_LOAD_MORE)
  },
  // 
  toggleLikedBox ({ commit }) {
    commit(types.TOGGLE_LIKED_BOX)
  },
  // 
  toggleCtrlBox ({ commit }) {
    commit(types.TOGGLE_CTRL_BOX)
  },
  // 
  setUserInfo ({ commit }, userInfo) {
    commit(types.SET_USER_INFO, userInfo)
  },
  // 
  toggleLoginState ({ commit }) {
    commit(types.TOGGLE_LOGIN_STATE)
  },
  // 
  toggleQRCodeShow ({ commit }) {
    commit(types.TOGGLE_QRCODE_SHOW)
  },
  // 
  setColumnTitle ({ commit }, name) {
    commit(types.SET_COLUMN_TITLE, name)
  }
}



