// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

// vue
import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'

//  vuex
import store from './store'

//  
import App from './App'
import NewsHome from './components/NewsHome.vue'
import NewsSetting from './components/NewsSetting.vue'
import NewsDetail from './components/NewsDetail.vue'
import NewsCollection from './components/NewsCollection.vue'
import UserLogin from './components/UserLogin.vue'
import SystemMessage from './components/SystemMessage.vue'
import AboutMe from './components/AboutMe.vue'
import ShareBox from './components/ShareBox.vue'
import NewsColumn from './components/NewsColumn.vue'

// 
Vue.use(VueRouter)
Vue.use(VueResource)

// 
const routes = [{
  // 
  path: '/zhihudaily/vue-zhihudaily/',
  component: NewsHome
}, {
  path: '/zhihudaily/vue-zhihudaily/home',
  component: NewsHome
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/setting',
  component: NewsSetting
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/article/:id',
  name: 'article',
  component: NewsDetail,
  children: [{ // 
    path: 'share',
    name: 'share',
    component: ShareBox
  }]
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/collect',
  component: NewsCollection
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/login',
  component: UserLogin
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/message',
  component: SystemMessage
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/aboutme',
  component: AboutMe
}, {
  // 
  path: '/zhihudaily/vue-zhihudaily/column/:id',
  name: 'column',
  component: NewsColumn
}]

// 
const router = new VueRouter({
  mode: 'hash', // history 404
  routes
})

// vue
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')




