import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

// Vuex
Vue.use(Vuex)

// state
const state = {
  curNews: {
    id: '',
    likes: '',
    comments: '',
    isLiked: '',
    isCollected: ''
  },
  // 
  user: {
    name: '',
    password: ''
  },

  columnTitle: '', // 

  news: [], // 
  collectList: [], // 

  /*  */
  isMaskShow: false, // mask
  isNightPattern: false, // 
  isMoreShow: false, // 
  isBoxShow: false, // 
  isCtrlShow: false, // 
  isUserLogin: false, // 
  isQRCodeShow: false // 
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})



